import React, { Component } from "react";
import TabGroup from "./tab_group";
import ContactList from "./contact_list";
import AddContact from "./add_contact";
import Dialer from "./dialer";
import { withStyles } from "@material-ui/core";
import HelpInfo from "./help_info";

const styles = {
  root: {
    backgroundColor: "rgb(245, 245,  245)",
    width: "80%",
    minHeight: "100vh",
    margin: "0 auto",
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-start",
    alignItems: "stretch"
  },
  main: {
    padding: "5px"
  }
};

class MainPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: "Dialer",
      contactsLoaded: false,
      contacts: [],
      currentNumber: ""
    };
  }

  setNumber = num => {
    this.setState({ currentNumber: num });
  };

  updateNumber = num => {
    this.setState(prevState => ({
      currentNumber: prevState.currentNumber + num
    }));
  };

  clearNumber = () => {
    this.setState({ currentNumber: "" });
  };

  setActiveTab = name => {
    this.setState({ activeTab: name });
    console.log("set active tab");
  };

  addContact = contact => {
    this.setState(prevState => ({
      contacts: [...prevState.contacts, contact]
    }));
  };

  showBody = activeTab => {
    switch (activeTab) {
      case "Contact List":
        if (!this.state.contactsLoaded) {
          fetch("https://jsonplaceholder.typicode.com/users")
            .then(response => response.json())
            .then(json => {
              this.setState(prevState => ({
                contacts: [...prevState.contacts, ...json],
                contactsLoaded: true
              }));
            });
          console.log("fetched");
        }

        return (
          <ContactList
            contacts={this.state.contacts}
            onClick={num => {
              this.setActiveTab("Dialer");
              this.setNumber(num);
            }}
          />
        );
      case "Add Contact":
        return <AddContact handleSubmit={this.addContact} />;
      case "Help":
        return <HelpInfo />;
      default:
        return (
          <Dialer
            updateNumber={this.updateNumber}
            clearNumber={this.clearNumber}
            currentNumber={this.state.currentNumber}
          />
        );
    }
  };

  render() {
    return (
      <div className={this.props.classes.root}>
        <TabGroup
          titles={["Dialer", "Contact List", "Add Contact", "Help"]}
          onClick={this.setActiveTab}
        />

        <div className={this.props.classes.main} role="main" aria-live="polite">
          {this.showBody(this.state.activeTab)}
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(MainPage);
