import React, { Component } from "react";
import { withStyles } from "@material-ui/core";

const styles = {
  contactButton: {
    backgroundColor: "white",
    border: "1px solid black",
    color: "black",
    textAlign: "center",
    fontSize: "20px",
    width: "100%"
  },
  container: {
    width: "80%",
    margin: "0 auto"
  }
};

class ContactList extends Component {
  formatPhoneNumber = num => num.split(" ")[0].replace(/\D/g, "");

  render() {
    if (!this.props.contacts) {
      return <p>Loading..</p>;
    }

    return (
      <div className={this.props.classes.container}>
        <table style={{ width: "100%" }}>
          <thead>
            <tr>
              <th>Name</th>
              <th>Number</th>
              <th>email</th>
            </tr>
          </thead>
          <tbody>
            {this.props.contacts.map((contact, i) => (
              <tr key={i}>
                <td>{contact["name"]}</td>
                <td
                  style={{ cursor: "pointer" }}
                  onClick={() =>
                    this.props.onClick(this.formatPhoneNumber(contact["phone"]))
                  }
                >
                  {this.formatPhoneNumber(contact["phone"])}
                </td>
                <td>{contact["email"]}</td>
              </tr>
            ))}
          </tbody>
        </table>

        {/* <Grid container direction="column" spacing={8}>
          {this.props.contacts.map((contact, i) => (
            <Grid item key={i}>
              <button
                className={this.props.classes.contactButton}
                onClick={() => this.props.onClick(contact["phone"])}
              >
                {contact["name"]}
              </button>
            </Grid>
          ))}
        </Grid> */}
      </div>
    );
  }
}

export default withStyles(styles)(ContactList);
